<template>
  <v-app>
    <div>
      <v-main>
        <v-app-bar color="#0c4c94" dark dense>
          <v-app-bar-nav-icon @click="drawer = true"></v-app-bar-nav-icon>

          <v-toolbar-title
            ><v-btn to="/home" class="text-decoration-none" plain>HOME</v-btn>
          </v-toolbar-title>

          <div v-if="showAdminBoard" class="d-none d-lg-flex d-xl-none">
            <!-- <router-link to="/admin">Admin Board</router-link> -->
            <v-btn to="/admin" class="text-decoration-none" plain
              >Admin Board
            </v-btn>
          </div>
          <div v-if="showModeratorBoard" class="d-none d-lg-flex d-xl-none">
            <v-btn to="/mod" class="text-decoration-none" plain
              >Moderator Board
            </v-btn>
          </div>
          <v-spacer></v-spacer>
          <div v-if="currentUser" class="d-none d-lg-flex d-xl-none">
            <v-btn to="/user" class="text-decoration-none" plain>User </v-btn>
            <v-btn to="/tutorials" class="text-decoration-none" plain
              >Tutorials
            </v-btn>
            <v-btn to="/add" class="text-decoration-none" plain>Add </v-btn>
          </div>

          <div v-if="!currentUser">
            <v-btn to="/register" class="text-decoration-none" plain
              ><font-awesome-icon icon="user-plus" />Sign Up
            </v-btn>

            <v-btn to="/login" class="text-decoration-none" plain
              ><font-awesome-icon icon="sign-in-alt" />Login
            </v-btn>
          </div>

          <div v-if="currentUser">
            <v-btn to="/profile" class="text-decoration-none" plain
              >{{ currentUser.username }}
            </v-btn>

            <v-btn @click.prevent="logOut" class="text-decoration-none" plain>
              LogOut
            </v-btn>
          </div>
        </v-app-bar>
        <div>
          <v-navigation-drawer
            v-if="currentUser"
            v-model="drawer"
            app
            temporary
            expand-on-hover
          >
            <v-list>
              <v-list-item class="px-2">
                <v-list-item-avatar>
                  <v-img
                    src="https://randomuser.me/api/portraits/women/85.jpg"
                  ></v-img>
                </v-list-item-avatar>
              </v-list-item>

              <v-list-item link>
                <v-list-item-content>
                  <v-list-item-title class="text-h6">
                    {{ currentUser.username }}
                  </v-list-item-title>
                  <v-list-item-subtitle>
                    {{ currentUser.email }}
                  </v-list-item-subtitle>
                  <v-list-item-subtitle class="text-h7">
                    <strong>Access:</strong>
                  </v-list-item-subtitle>
                  <v-list-item-subtitle class="text-h8">
                    <ul>
                      <li
                        v-for="(role, index) in currentUser.roles"
                        :key="index"
                      >
                        {{ role }}
                      </li>
                    </ul>
                  </v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-list>

            <v-divider></v-divider>

            <!-- User Links -->

            <v-list nav dense>
              <div v-for="(link, i) in links" :key="i">
                <v-list-item
                  v-if="!link.subLinks"
                  :to="link.to"
                  class="v-list-item text-decoration-none"
                >
                  <v-list-item-icon>
                    <v-icon>{{ link.icon }}</v-icon>
                  </v-list-item-icon>

                  <v-list-item-title v-text="link.text" />
                </v-list-item>

                <v-list-group
                  v-else
                  :key="link.text"
                  no-action
                  :prepend-icon="link.icon"
                  :value="false"
                >
                  <template v-slot:activator>
                    <v-list-item-title>{{ link.text }}</v-list-item-title>
                  </template>

                  <v-list-item
                    v-for="sublink in link.subLinks"
                    :to="sublink.to"
                    :key="sublink.text"
                    class="text-decoration-none px-2"
                  >
                    <v-list-item-icon>
                      <v-icon>{{ sublink.icon }}</v-icon>
                    </v-list-item-icon>
                    <v-list-item-title>{{ sublink.text }}</v-list-item-title>
                  </v-list-item>
                </v-list-group>
              </div>
            </v-list>

            <!-- admin links -->

            <v-list v-if="showAdminBoard" nav dense>
              <div v-for="(link, i) in adminlinks" :key="i">
                <v-list-item
                  v-if="!link.subLinks"
                  :to="link.to"
                  class="v-list-item text-decoration-none"
                >
                  <v-list-item-icon>
                    <v-icon>{{ link.icon }}</v-icon>
                  </v-list-item-icon>

                  <v-list-item-title v-text="link.text" />
                </v-list-item>

                <v-list-group
                  v-else
                  :key="link.text"
                  no-action
                  :prepend-icon="link.icon"
                  :value="false"
                >
                  <template v-slot:activator>
                    <v-list-item-title>{{ link.text }}</v-list-item-title>
                  </template>

                  <v-list-item
                    v-for="sublink in link.subLinks"
                    :to="sublink.to"
                    :key="sublink.text"
                    class="text-decoration-none px-2"
                  >
                    <v-list-item-icon>
                      <v-icon>{{ sublink.icon }}</v-icon>
                    </v-list-item-icon>
                    <v-list-item-title>{{ sublink.text }}</v-list-item-title>
                  </v-list-item>
                </v-list-group>
              </div>
            </v-list>
          </v-navigation-drawer>
        </div>

        <router-view />

        <!-- <div class="container"></div> -->
      </v-main>
    </div>

    <Footer />
  </v-app>
</template>


<script>
import EventBus from "./common/EventBus";
import Footer from "./components/footer.vue";

export default {
  components: {
    Footer,
  },
  data: () => ({
    drawer: false,
    // group: null,
    // admins: [
    //   ["Management", "mdi-account-multiple-outline"],
    //   ["Settings", "mdi-cog-outline"],
    // ],
    // cruds: [
    //   ["Create", "mdi-plus-outline"],
    //   ["Read", "mdi-file-outline"],
    //   ["Update", "mdi-update"],
    //   ["Delete", "mdi-delete"],
    // ],

    adminlinks: [
      // {
      //   to: "/home",
      //   icon: "mdi-view-dashboard",
      //   text: "Dashboard",
      // },
      {
        icon: "mdi-shield-lock-outline",
        text: "Admin",
        subLinks: [
          {
            text: "User Management",
            to: "/admin",
            icon: "mdi-account-multiple-outline",
          },
          // {
          //   text: "Settings",
          //   to: "/templates/new",
          //   icon: "mdi-plus",
          // },
          {
            text: "Settings",
            to: "/settings",
            icon: "mdi-cog-outline",
          },
        ],
      },
    ],

    links: [
      {
        to: "/home",
        icon: "mdi-view-dashboard",
        text: "Dashboard",
      },
      {
        icon: "mdi-cube-outline",
        text: "Department",
        subLinks: [
          {
            text: "Bodily Injury (BI)",
            to: "/b-i",
            icon: "mdi-human",
          },
          {
            text: "Own Damage (OD)",
            to: "/o-d",
            icon: "mdi-human-greeting",
          },
          {
            text: "Non Motor (NM)",
            to: "/n-m",
            icon: "mdi-fire",
          },
          {
            text: "Articles",
            to: "/articles",
            icon: "mdi-fire",
          },
        ],
      },
    ],
  }),
  // name: 'App',
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
    showAdminBoard() {
      if (this.currentUser && this.currentUser.roles) {
        return this.currentUser.roles.includes("ROLE_ADMIN");
      }

      return false;
    },
    showModeratorBoard() {
      if (this.currentUser && this.currentUser.roles) {
        return this.currentUser.roles.includes("ROLE_MODERATOR");
      }

      return false;
    },
  },
  methods: {
    logOut() {
      this.$store.dispatch("auth/logout");
      this.$router.push("/login");
    },
  },
  mounted() {
    EventBus.on("logout", () => {
      this.logOut();
    });
  },
  beforeDestroy() {
    EventBus.remove("logout");
  },
};
</script>

